@import url(https://fonts.googleapis.com/css?family=Pontano+Sans);
@import url(https://fonts.googleapis.com/css?family=Cardo:400,700);
body {
  margin: 0;
  padding: 0;
  font-family: 'Pontano Sans', serif;
}

:root {
  --max-content-width: 1170px;
  --md-screen-size: 768px;
  --xs-screen-size: 480px;
  --text-color: #d8d5d5;
  --main-menu-color: #4e4e50;
}

.loaderContainer {
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	transition: ease-out 2s;
	width: 100%;
}
.headerTitle {
	font-family: 'Cardo', serif;
	letter-spacing:	10px;
	font-size: 80px;
	text-transform: uppercase;
	color:white;
	opacity:.85;
	border-bottom: 3px solid #6d101a;
}
.sub {
	text-transform: uppercase;
	text-align: center;
 	letter-spacing:29px;
	font-size: 35px;
 	margin-left:9px;
 	color:#6d101a;
 	-webkit-filter:blur(1px);
 	        filter:blur(1px);
}

.loader {
	display: flex;
    justify-content: center;
	width: 100%;
    height: 85px;
  	margin-top:50px;
}
.loader span {
  	display:block;
  	float:left;
  	background:white;
  	height:5px;
  	opacity:0;
  	-webkit-filter:blur(1px);
  	        filter:blur(1px);
  	-webkit-animation: fade 4.5s ease-out infinite;
  	        animation: fade 4.5s ease-out infinite;
}
.loader .h {
  	-webkit-animation: fadelong 4.5s ease-out infinite;
  	        animation: fadelong 4.5s ease-out infinite;
}
.loader span:nth-child(1) {
	width:2px;
	margin-right:1px;
	-webkit-animation-delay: .75s;
	        animation-delay: .75s;
}
