@-webkit-keyframes fade{
  0%{height:2%;}
  1%{opacity:1;}
  50%{height:90%;}
  70%{opacity:1;}
  95%{opacity:0;}
  100%{height:90%;}
}
@keyframes fade{
  0%{height:2%;}
  1%{opacity:1;}
  50%{height:90%;}
  70%{opacity:1;}
  95%{opacity:0;}
  100%{height:90%;}
}
@-webkit-keyframes fadelong{
  0%{height:2%;}
  1%{opacity:1;}
  50%{height:100%;}
  70%{opacity:1;}
  95%{opacity:0;}
  100%{height:100%;}
}
@keyframes fadelong{
  0%{height:2%;}
  1%{opacity:1;}
  50%{height:100%;}
  70%{opacity:1;}
  95%{opacity:0;}
  100%{height:100%;}
}

.infoContainer {
	display: flex;
	flex: 1 1;
	width: 100%;
	max-width: var( --max-content-width);
	margin: auto;
	height: 100%;
}

.infoContainer > div {
	display: flex;
	flex-basis: calc(50% - 20px);
	align-items: center;
	justify-content: center;
	padding: 0 20px;
}

/* .imageContainer {
	align-items: center;
	justify-content: center;
} */

.imageContainer > img{
	max-height: 800px;
	max-width: 600px;
}

.info {
	flex-direction: column;
}

.info > h2 {
	width: 100%;
	/* background: var(--main-menu-color); */
	letter-spacing: 2px;
	font-size: 30px;
	color: var(--text-color);
}

.stats {
	display: flex;
	flex-direction: column;
	width: 100%;
	letter-spacing: 2px;
	color: var(--text-color);
}

@media (max-width: 768px) {
	.infoContainer {
		flex-direction: column;
	}
}

.cvContainer {
	display: flex;
	flex-direction: column;
	width: 100%;
}

.navContainer {
	display: flex;
	max-height: 60px;
	min-height: 60px;
	width: 100%;
	background: var(--main-menu-color);
}

.nav {
	display: flex;
	width: 100%;
	margin: 0 auto;
	padding: 0;
	max-width: var( --max-content-width);
	list-style-type: none;
}

.nav > li {
	min-width: 150px;
    height: 100%;
	cursor: pointer;
	text-transform: uppercase;
	font-size: 20px;
}

a {
	width: 100%;
    height: 100%;
	display: flex;
    justify-content: center;
	align-items: center;
	text-decoration: none;
	color: var(--text-color);
	outline: none;
    letter-spacing: 2px;

}
.nav > li > a:hover {
	font-weight: 600;
}

.nav > li > a:focus, a:active {
	background: #0a0a0a;
	font-weight: 600;
}


.App {
  background: #0a0a0a;
  height: 100vh;
  display: flex;
  overflow: auto;
}

